import {
  Button,
  Container,
  Divider,
  FormControl,
  FormControlLabel,
  Grid,
  Radio,
  RadioGroup,
  SliderMarkLabel,
  Stack,
  TextField,
  Typography,
} from "@material-ui/core";
import React, { useState, useEffect, useRef } from "react";
import SimpleBar from "simplebar-react";
import { styled } from "@material-ui/styles";

//
import { useWindowDimensions } from "../../../../../utils/helperFunction";
import Image1 from "../../../../../assets/donation/1.png";
import Image2 from "../../../../../assets/donation/2.png";
import Image3 from "../../../../../assets/donation/3.png";
import Image4 from "../../../../../assets/donation/4.png";
import DonationSlider from "./DonationSlider";

import { donationIntialValue } from "../../../../../utils/initialvalues";
import {
  AddFunction,
  CreateTransactionType,
  MakeDonationProps,
  RootAppState,
} from "../../../../../utils/typeHelpers";

import { PayPalButtons } from "@paypal/react-paypal-js";
import {
  CREATE_PAYMENT_API,
  CREATE_TRANSACTION_API,
} from "../../../../../utils/Api";
import { useSelector } from "react-redux";
import "./simple.css";

import { MessageError } from "../../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../../components/AlertMessageSuccess";
import { useNavigate } from "react-router";

const RightContainer = ({
  goalID,
  maxValueOfSlider,
}: {
  goalID: number;
  maxValueOfSlider: number;
}) => {
  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);

  const { height } = useWindowDimensions();

  const userInfo = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo
  );
  const [donation, setDonation] = useState<MakeDonationProps>({
    ...donationIntialValue,
    goal: goalID,
  });
  const [sdkReady, setSdkReady] = useState(false);
  const [value, setValue] = useState("");

  const [showGift, setShowGift] = useState(false);

  const handleChangeAmount = (
    event: Event,
    newDonationValue: number | number[]
  ) => {
    if (typeof newDonationValue === "number") {
      setDonation({ ...donation, amount: newDonationValue });
    }
  };

  const handleDonationButton = (e: number) => {
    if (e !== 0) {
      setDonation({ ...donation, amount: e });
    }
  };

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };

  const handleCreatePayment = () => {
    // console.log('**donation: * ', donation);

    if (donation.amount > 0) {
      if (donation.is_dedicated === true && donation.dedicated_name === "") {
        handleCreateError("Dedication name can't be empty");
      } else if (
        donation.is_dedicated === true &&
        donation.dedicated_email === ""
      ) {
        handleCreateError("Dedication email can't be empty");
      } else if (
        donation.is_dedicated === true &&
        /(.+)@(.+){2,}\.(.+){2,}/.test(donation.dedicated_email) === false
      ) {
        handleCreateError("Dedication email should be valid");
      } else if (
        donation.is_dedicated === true &&
        donation.dedicated_message === ""
      ) {
        handleCreateError("Dedication message can't be empty");
      } else if (
        donation.is_gift_dedicated === true &&
        donation.dedicated_gift_message === ""
      ) {
        handleCreateError("Gift message can't be empty");
      } else {
        setSdkReady(true);
      }
    } else {
      handleCreateError("Donation Can't be 0");
    }
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setShowGift(event.target.checked);
    // console.log('showGift :', showGift);

    if (!event.target.checked) {
      setValue("");
      setDonation({ ...donation, is_dedicated: false });
    } else {
      setValue(event.target.value);
      setDonation({ ...donation, is_dedicated: true });
    }
  };

  return (
    <Grid item xs={12} sm={6} md={8}>
      <MessageError
        children={(add: AddFunction) => {
          refError.current = add;
        }}
      />
      <MessageSuccess
        children={(add: AddFunction) => {
          refSuccess.current = add;
        }}
      />
      <SimpleBar
        style={{ maxHeight: height - 60 }}
        forceVisible="y"
        autoHide={false}
      >
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="center"
          spacing={1}
          sx={{ padding: 2 }}
        >
          <DonationSlider
            handleChangeAmount={handleChangeAmount}
            amount={donation.amount}
            handleRadioChangeSlider={handleChange}
            value={showGift}
            maxValueOfSlider={maxValueOfSlider}
            handleDonationButton={handleDonationButton}
          />
          {showGift && (
            <Container maxWidth="md">
              <Box className="donation_amount">
                <h2>Dedicatory information</h2>

                <FormControl fullWidth component="fieldset" variant="standard">
                  {/* <RadioGroup
                    aria-label="donation"
                    name="donation"
                    value={value}
                  >
                    <FormControlLabel
                      value="true"
                      control={<Radio onClick={handleRadioChange} />}
                      label="Dedicate my donation"
                    />
                  </RadioGroup> */}

                  <TextField
                    style={{ paddingTop: "14px", paddingBottom: "12px" }}
                    name="name"
                    label="Name"
                    defaultValue=""
                    value={donation.dedicated_name}
                    onChange={(e) =>
                      setDonation({
                        ...donation,
                        dedicated_name: e.target.value,
                      })
                    }
                  />

                  <TextField
                    style={{ paddingTop: "14px", paddingBottom: "12px" }}
                    name="email"
                    label="Email"
                    defaultValue=""
                    value={donation.dedicated_email}
                    onChange={(e) =>
                      setDonation({
                        ...donation,
                        dedicated_email: e.target.value,
                      })
                    }
                  />

                  <TextField
                    name="message"
                    label="Message"
                    multiline
                    rows={4}
                    defaultValue=""
                    value={donation.dedicated_message}
                    onChange={(e) =>
                      setDonation({
                        ...donation,
                        dedicated_message: e.target.value,
                      })
                    }
                  />
                </FormControl>
              </Box>
            </Container>
          )}
          {!sdkReady && (
            <Button
              sx={{ margin: 2 }}
              variant="contained"
              onClick={handleCreatePayment}
            >
              Donate
            </Button>
          )}
          {sdkReady && (
            <PayPalButtons
              style={{ layout: "horizontal" }}
              createOrder={(data, actions) => {
                return actions.order.create({
                  purchase_units: [
                    {
                      amount: {
                        value: donation.amount.toString(),
                      },
                    },
                  ],
                });
              }}
              onApprove={async (data, actions) => {
                const dataRes = await CREATE_PAYMENT_API(
                  donation,
                  userInfo.access
                );
                console.log(dataRes);

                return actions.order.capture().then(function (details) {
                  const transactionData: CreateTransactionType = {
                    order_id: details.id,
                    payment: dataRes.id,
                  };

                  if (details.status === "COMPLETED") {
                    CREATE_TRANSACTION_API(
                      transactionData,
                      userInfo.access
                    ).then((res) => {
                      if (res === "error") {
                        handleCreateError("Donation failed");
                      } else {
                        handleCreateSucess("Donation is Completed");
                      }
                    });
                  }

                  // setTimeout(() => {
                  //   navigate("../");
                  // }, 10000);
                });
              }}
              onCancel={(data, actions) => {
                setSdkReady(false);

                return actions.redirect();
              }}
            />
          )}
        </Stack>
      </SimpleBar>
    </Grid>
  );
};

export default RightContainer;

const Box = styled("div")(() => ({
  minHeight: 150,
  borderRadius: 24,
  border: "1px solid #48CAE4",
  boxSizing: "-moz-initial",
  padding: 20,
}));

const ProductImage = styled("img")(() => ({
  height: 70,
  width: 70,
  borderRadius: 10,
}));

const demoData = [
  {
    image: Image1,
    title: "Football",
  },
  {
    image: Image2,
    title: "Shoes",
  },
  {
    image: Image3,
    title: "Shin guards",
  },
  {
    image: Image4,
    title: "Disc cones",
  },
];
