import { Grid, Container } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router";

import LeftContainer from "./components/LeftContainer";
import RightContainer from "./components/RightContainer";
import { useSelector } from "react-redux";
import { useQuery, UseQueryResult } from "react-query";

import ErrorCatchComponent from "../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../Errror/ApiErrorPage";
import Loader from "../../../../components/Loader";
import useAxios from "../../../../utils/useAxios";
import {
  RootAppState,
  SingleGoalType,
  StateRouteProps,
} from "../../../../utils/typeHelpers";

const MakeDonation = () => {
  const navigate = useNavigate();

  const userState = useSelector((state: RootAppState) => state.AuthReducer);
  const [maxValueOfSlider, setMaxValueOfSlider] = useState(0);

  const location = useLocation();
  const state: StateRouteProps = location.state ? location.state : { slug: "" };

  const [refreshState, setRefreshState] = useState(true);
  const cngRefreshState = () => {
    setRefreshState(!refreshState);
  };

  const axiosInstance = useAxios();
  const {
    data: goalInfo,
    isLoading,
    isError,
    error,
  }: UseQueryResult<SingleGoalType, Error> = useQuery(
    [
      "getSingleGoal",
      [
        state?.slug,
        userState.isLoggedIn ? userState.userInfo.access : "",
        false,
        refreshState,
      ],
    ],
    async () => {
      const response = await axiosInstance.get(`/retrieve-goal/${state.slug}`);

      return response.data;
    }
  );

  useEffect(() => {
    if (!userState.userInfo.is_account_created) {
      navigate("../createprofiledonor");
    }
    if (goalInfo !== null && goalInfo) {
      const maxValue =
        parseFloat(goalInfo.total_amount ? goalInfo.total_amount : "0") -
        parseFloat(goalInfo.paid_amount ? goalInfo.paid_amount : "0");
      setMaxValueOfSlider(maxValue);
    }
  }, [userState.userInfo.is_account_created, goalInfo]);

  return (
    <>
      <Container>
        <ErrorCatchComponent>
          {isLoading === false ? (
            <div>
              {isError ? (
                <ApiErrorPage
                  error={
                    error ? error : new Error("Network response was not ok.")
                  }
                />
              ) : goalInfo !== null && goalInfo !== undefined ? (
                <Grid container component="main">
                  <LeftContainer
                    data={goalInfo}
                    cngRefreshState={cngRefreshState}
                  />
                  <RightContainer
                    goalID={goalInfo.id}
                    maxValueOfSlider={maxValueOfSlider}
                  />
                </Grid>
              ) : (
                <div>
                  <Loader />
                </div>
              )}
            </div>
          ) : (
            <Loader />
          )}
        </ErrorCatchComponent>
      </Container>
    </>
  );
};

export default MakeDonation;
