import React from "react";
import {
  Grid,
  TextField,
  Container,
  Box,
  Slider,
  FormControlLabel,
  FormControl,
  Button,
  Checkbox,
} from "@material-ui/core";
import "./simple.css";

function valueLabelFormat(value: number) {
  let scaledValue = value;
  return `${scaledValue} $`;
}

export default function DonationSlider({
  handleChangeAmount,
  handleDonationButton,
  handleRadioChangeSlider,
  amount,
  value,
  maxValueOfSlider,
}: {
  handleDonationButton: any;
  handleChangeAmount: any;
  handleRadioChangeSlider: any;
  amount: number;
  maxValueOfSlider?: number;
  value: boolean;
}) {
  const randDonationValue = [10, 30, 50, 100, 150];

  return (
    <Container maxWidth="md">
      <Box className="donation_amount">
        <h2>Donation amount</h2>
        <h3>${amount}</h3>
        <Slider
          value={amount}
          min={1}
          step={0.1}
          max={maxValueOfSlider}
          getAriaValueText={valueLabelFormat}
          valueLabelFormat={valueLabelFormat}
          onChange={handleChangeAmount}
          valueLabelDisplay="auto"
          aria-labelledby="non-linear-slider"
        />

        <Grid className="random-donation-div" container spacing={2}>
          {randDonationValue.map((donationVal) => (
            <Grid item xs={2}>
              <Button
                onClick={() => handleDonationButton(donationVal)}
                className="random-donation"
              >
                {donationVal.toLocaleString()}
              </Button>
            </Grid>
          ))}
          <Grid item xs={2}>
            <Button
              onClick={() => handleDonationButton(maxValueOfSlider)}
              className="random-donation"
            >
              Fulfill
            </Button>
          </Grid>
        </Grid>

        <Grid className="set-donation-amount" container spacing={2}>
          <Grid item xs={3}>
            <Box sx={{ paddingTop: "10px" }}>
              <span>Donation amount</span>
            </Box>
          </Grid>
          <Grid item xs={3}>
            <FormControl>
              <TextField
                label="$"
                value={amount.toLocaleString()}
                type="number"
                onChange={(e) => handleDonationButton(e.target.value)}
              />
            </FormControl>
          </Grid>
        </Grid>

        <FormControlLabel
          label="Dedicate my donation"
          control={
            <Checkbox checked={value} onChange={handleRadioChangeSlider} />
          }
        />
      </Box>
    </Container>
  );
}
